import { createStyledAttrsMixin } from '@mekari/pixel-utils';

const useTruncated = () => ({
  display: '-webkit-box',
  overflow: 'hidden',
  textOverflow: 'ellipsis'
});
const useStriked = () => ({
  textDecoration: 'line-through'
});
const useLink = () => ({
  cursor: 'pointer',
  color: 'blue.400',
  _hover: {
    color: 'blue.500',
    textDecoration: 'underline'
  },
  _active: {
    color: 'blue.500'
  }
});

const lineHeights = {
  xs: '1sm',
  sm: 'xxlg',
  md: '3lg',
  lg: 'lg',
  xl: '1lg',
  '2xl': 'xxlg'
};
/**
 * Usage :** ` import { MpText } from '@mekari/pixel' `
 **/

const MpText = {
  name: 'MpText',
  mixins: [createStyledAttrsMixin('MpText')],
  props: {
    as: {
      type: [String, Array],
      default: 'p'
    },
    fontFamily: {
      type: [String, Array],
      default: 'body'
    },
    fontWeight: {
      type: [String, Array],
      default: 'regular'
    },
    fontSize: {
      type: [String],
      default: 'md'
    },
    fontVariantNumeric: {
      type: [String]
    },
    lineClamp: {
      type: [String, Number]
    },
    isTruncated: Boolean,
    isStriked: Boolean,
    isLink: Boolean
  },
  computed: {
    componentStyles() {
      return {
        fontFamily: this.as === 'kbd' ? 'mono' : this.fontFamily,
        lineHeight: lineHeights[this.fontSize],
        fontWeight: this.fontWeight ? this.fontWeight : 'regular',
        fontSize: this.fontSize ? this.fontSize : 'md',
        fontVariantNumeric: this.fontVariantNumeric,
        color: 'dark',
        ...(this.isTruncated && useTruncated()),
        ...(this.isStriked && useStriked()),
        ...(this.isLink && useLink())
      };
    }

  },

  render(h) {
    return h(this.as, {
      class: this.className,
      attrs: this.computedAttrs,
      style: { ...(this.isTruncated && {
          '-webkit-line-clamp': this.lineClamp || 1,
          '-webkit-box-orient': 'vertical'
        })
      }
    }, this.$slots.default);
  }

};

export { MpText, useLink, useStriked, useTruncated };
