import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { useTruncated } from '@mekari/pixel-text';

const lineHeights = {
  '2xl': 'xxlg',
  xl: '1lg',
  lg: 'lg'
};
/**
 * Usage :** ` import { MpHeading } from '@mekari/pixel' `
 **/

const MpHeading = {
  name: 'MpHeading',
  mixins: [createStyledAttrsMixin('MpHeading')],
  props: {
    fontSize: {
      type: String,
      default: '2xl'
    },
    as: {
      type: [String, Array],
      default: 'h1'
    },
    isTruncated: Boolean
  },
  computed: {
    componentStyles() {
      return {
        fontSize: this.fontSize,
        lineHeight: lineHeights[this.fontSize],
        fontWeight: 'semibold',
        fontFamily: 'body',
        ...(this.isTruncated && useTruncated())
      };
    }

  },

  render(h) {
    return h(this.as, {
      class: this.className,
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};

export { MpHeading };
