import { useId, cloneVNode, wrapEvent, forwardProps } from '@mekari/pixel-utils';
import { MpFragment } from '@mekari/pixel-fragment';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';
import { MpPopper, MpPopperArrow } from '@mekari/pixel-popper';
import { MpBox } from '@mekari/pixel-box';
import Vue from 'vue';
import Pixel, { ThemeProvider } from '@mekari/pixel';
import { createPopper } from '@popperjs/core';

const baseStyle = {
  px: '2',
  py: '1',
  borderRadius: 'sm',
  fontWeight: 'regular',
  pointerEvents: 'none',
  fontSize: 'sm',
  shadow: 'md',
  maxW: 'xs',
  zIndex: 'tooltip',
  'box-shadow': 'sm'
};
const useTooltipStyles = props => {
  return { ...baseStyle,
    bg: props.colorMode === 'dark' ? 'gray.400' : 'overlay',
    color: props.colorMode === 'dark' ? 'gray' : 'white'
  };
};

const tooltipProps = {
  label: String,
  _ariaLabel: String,
  showDelay: {
    type: Number,
    default: 0
  },
  hideDelay: {
    type: Number,
    default: 0
  },
  position: {
    type: String,
    default: 'top'
  },
  hasArrow: Boolean,
  closeOnClick: Boolean,
  defaultIsOpen: Boolean,
  shouldWrapChildren: Boolean,
  controlledIsOpen: Boolean,
  isControlled: Boolean,
  onOpen: Function,
  onClose: Function,
  id: String,
  usePortal: Boolean
};
const MpTooltip = {
  name: 'MpTooltip',
  inject: ['$pixelColorMode'],
  inheritAttrs: false,

  data() {
    return {
      isOpen: this.isControlled ? this.controlledIsOpen : this.defaultIsOpen || false,
      enterTimeout: null,
      exitTimeout: null,
      tooltipAnchor: undefined,
      noop: 0
    };
  },

  computed: {
    colorMode() {
      return this.$pixelColorMode();
    },

    tooltipId() {
      return this.id || `tooltip-${useId(4)}`;
    },

    componentStyles() {
      return useTooltipStyles({
        colorMode: this.colorMode
      });
    }

  },
  watch: {
    controlledIsOpen(val) {
      if (this.isControlled) {
        if (val) {
          this.handleOpen();
        } else {
          this.handleClose();
        }
      }
    }

  },
  methods: {
    open() {
      this.isOpen = true;
    },

    close() {
      this.isOpen = false;
    },

    openWithDelay() {
      this.enterTimeout = setTimeout(this.open, this.showDelay);
    },

    closeWithDelay() {
      this.exitTimeout = setTimeout(this.close, this.hideDelay);
    },

    handleOpen() {
      this.openWithDelay();
      clearInterval(this.exitTimeoutx);
      this.$emit('open');
    },

    handleClose() {
      this.closeWithDelay();
      clearInterval(this.enterTimeout);
      this.$emit('close');
    },

    handleOpenFocus() {
      if (!this.isControlled) this.openWithDelay();
    },

    handleCloseBlur() {
      if (!this.isControlled) this.closeWithDelay();
    },

    handleClick() {
      this.closeOnClick && this.closeOnClick();
      this.$emit('click');
    },

    handleKeydown(event) {
      const {
        key
      } = event;

      if (key === 'Escape') {
        this.handleClose();
      }
    }

  },
  props: tooltipProps,

  mounted() {
    this.$nextTick(() => {
      this.noop++;
      this.tooltipAnchor = document.querySelector(`[x-tooltip-anchor=${this.tooltipId}]`);
    });
  },

  render(h) {
    let clone;
    const hasAriaLabel = this._ariaLabel !== undefined;
    const children = this.$slots.default;

    if (children.length > 1) {
      return console.error('[Pixel]: The MpTooltip component only expects one child.');
    }

    if (children[0].text || this.shouldWrapChildren) {
      clone = clone = h(MpBox, {
        props: {
          as: 'span'
        },
        attrs: {
          tabindex: 0,
          'x-tooltip-anchor': `${this.tooltipId}`,
          ...(this.isOpen && {
            'aria-describedby': this.tooltipId
          })
        },
        on: { ...(!this.isControlled && {
            mouseenter: this.handleOpen,
            mouseleave: this.handleClose,
            focus: this.handleOpen
          }),
          click: this.handleClick,
          blur: this.handleClose,
          keydown: this.handleKeydown
        },
        ref: 'tooltipRef'
      }, children[0].text);
    } else {
      const cloned = cloneVNode(children[0], h);

      if (cloned.componentOptions) {
        clone = h(cloned.componentOptions.Ctor, { ...cloned.data,
          ...(cloned.componentOptions.listeners || {}),
          props: { ...(cloned.data.props || {}),
            ...cloned.componentOptions.propsData
          },
          attrs: { ...cloned.data.attrs,
            ...(this.isOpen && {
              'aria-describedby': this.tooltipId
            }),
            'x-tooltip-anchor': `${this.tooltipId}`
          },
          on: cloned.componentOptions.listeners,
          nativeOn: { ...(!this.isControlled && {
              mouseenter: this.handleOpen,
              mouseleave: this.handleClose,
              focus: this.handleOpenFocus
            }),
            click: wrapEvent(this.handleClick, e => this.$emit('click', e)),
            blur: this.handleCloseBlur,
            keydown: this.handleKeydown
          }
        }, cloned.componentOptions.children);
      }
    }

    return h(MpFragment, [clone, h(MpPopper, {
      props: {
        usePortal: this.usePortal,
        anchorEl: this.tooltipAnchor,
        hasArrow: true,
        isOpen: this.isOpen,
        placement: this.position,
        modifiers: [{
          name: 'offset',
          options: {
            offset: [0, 8]
          }
        }],
        arrowSize: '10px',
        ...forwardProps(this.$props)
      },
      attrs: { ...this.componentStyles,
        ...this.$attrs,
        id: hasAriaLabel ? undefined : this.tooltipId,
        role: hasAriaLabel ? undefined : 'tooltip',
        'data-noop': this.noop,
        'data-pixel-component': 'MpTooltip'
      }
    }, [h(MpBox, {
      props: {
        as: 'span'
      },
      ...(!this.$slots.label && {
        domProps: {
          innerHTML: this.label
        }
      }),
      attrs: {
        whiteSpace: 'normal',
        overflowWrap: 'break-word'
      }
    }, [this.$slots.label && this.$slots.label]), hasAriaLabel && h(MpVisuallyHidden, {
      attrs: {
        role: 'tooltip',
        id: this.tooltipId
      }
    }, this._ariaLabel), this.hasArrow && h(MpPopperArrow)])]);
  }

};

const MpSingletonTooltip = {
  name: 'MpSingletonTooltip',
  inject: ['$pixelColorMode'],
  props: {
    id: String,
    isOpen: {
      type: [Boolean],
      default: false
    },
    label: {
      type: [String],
      default: ''
    }
  },
  computed: {
    colorMode() {
      return this.$pixelColorMode();
    },

    componentStyles() {
      return useTooltipStyles({
        colorMode: this.colorMode
      });
    }

  },

  render(h) {
    return h(MpBox, {
      props: {
        as: 'div'
      },
      domProps: {
        innerHTML: this.label
      },
      style: {
        display: this.isOpen && this.label ? 'unset' : 'none'
      },
      attrs: {
        id: this.id,
        ...this.componentStyles,
        ...this.$attrs,
        'x-tooltip-anchor': this.id,
        role: 'tooltip',
        ...(this.isOpen && {
          'aria-describedby': this.tooltipId
        })
      }
    });
  }

};

const isBrowser = typeof window !== 'undefined' && typeof window.document !== 'undefined';
const PORTAL_ID = 'pixel-singleton-tooltip-kitchen';
const ROOT_ID = 'pixel-singleton-tooltip-root';
const TOOLTIP_ID = 'pixel-singleton-tooltip';
const VM = typeof window !== 'undefined' && window.Vue ? window.Vue : Vue; // Store

const store = Vue.observable({
  popperInstance: null,
  isOpen: false,
  label: '',
  timeoutOpen: null,
  timeoutClose: null
});

function createPortal() {
  if (!isBrowser) {
    return;
  }

  let portalElement;
  const existingPortalElement = document.getElementById(PORTAL_ID);

  if (existingPortalElement) {
    portalElement = existingPortalElement;
  } else {
    const el = document.createElement('div');
    el.id = PORTAL_ID;

    if (document.body != null) {
      document.body.appendChild(el);
    }

    portalElement = el;
  }

  const root = document.createElement('div');
  root.id = ROOT_ID;
  portalElement.appendChild(root);
  return portalElement;
}

function createSingletonTooltip() {
  if (!isBrowser) {
    return;
  }

  Vue.nextTick(() => {
    VM.use(Pixel);
    new VM({
      name: 'SingletonTooltip',
      render: h => h(ThemeProvider, {
        id: 'tooltip-provider'
      }, [h(MpSingletonTooltip, {
        props: {
          id: TOOLTIP_ID,
          isOpen: store.isOpen,
          label: store.label
        }
      })])
    }).$mount(`#${ROOT_ID}`);
  });
}

function createPopperInstance() {
  if (!isBrowser) {
    return;
  }

  Vue.nextTick(() => {
    // Set temporary reference to body
    const temporaryReference = document.body;
    const tooltip = document.getElementById(TOOLTIP_ID); // Save popper instance

    store.popperInstance = createPopper(temporaryReference, tooltip, {
      placement: 'top',
      modifiers: [{
        name: 'offset',
        options: {
          offset: [0, 8]
        }
      }]
    });
  });
}

class SingletonTooltip {
  constructor() {
    createPortal();
    createSingletonTooltip();
  } // State


  get state() {
    return store;
  } // Mutation


  async handleOpenTooltip(el, options) {
    const {
      label,
      showDelay,
      position
    } = options;

    if (!store.popperInstance) {
      createPopperInstance();
    }

    clearTimeout(store.timeoutOpen);
    store.timeoutOpen = setTimeout(async () => {
      store.isOpen = true;
      store.label = label || options; // Update popper instance

      store.popperInstance.state.elements.reference = el;
      store.popperInstance.setOptions({
        placement: position || 'top'
      });
      await store.popperInstance.update();
    }, showDelay || 0);
  }

  handleCloseTooltip(el, options) {
    const {
      hideDelay
    } = options;
    clearTimeout(store.timeoutClose);
    store.timeoutOpen = setTimeout(() => {
      store.isOpen = false;
      store.label = ''; // Revert label to default
    }, hideDelay || 0);
  }

}

var singletonTooltip = new SingletonTooltip();

function applyTooltip(el, options) {
  el.$pixelTooltipOpenHandler = () => singletonTooltip.handleOpenTooltip(el, options.value);

  el.$pixelTooltipCloseHandler = () => singletonTooltip.handleCloseTooltip(el, options.value);

  el.addEventListener('mouseover', el.$pixelTooltipOpenHandler);
  el.addEventListener('focus', el.$pixelTooltipOpenHandler);
  el.addEventListener('mouseleave', el.$pixelTooltipCloseHandler);
  el.addEventListener('blur', el.$pixelTooltipCloseHandler);
}

function removeTooltip(el) {
  el.removeEventListener('mouseover', el.$pixelTooltipOpenHandler);
  el.removeEventListener('focus', el.$pixelTooltipOpenHandler);
  el.removeEventListener('mouseleave', el.$pixelTooltipCloseHandler);
  el.removeEventListener('blur', el.$pixelTooltipCloseHandler);
  delete el.$pixelTooltipOpenHandler;
}

const tooltipDirective = {
  bind(el, binding) {
    // Bind directive only if label exist
    if (binding.value) {
      applyTooltip(el, binding);
    }
  },

  componentUpdated(el, binding) {
    removeTooltip(el);
    Vue.nextTick(() => {
      applyTooltip(el, binding);
    });
  },

  unbind(el) {
    removeTooltip(el);
  }

};

export { MpTooltip, tooltipDirective };
