import { SNA, createStyledAttrsMixin } from '@mekari/pixel-utils';

const flexProps = {
  as: {
    type: String,
    default: 'div'
  },
  align: SNA,
  justify: SNA,
  wrap: SNA,
  direction: SNA,
  size: SNA
};
/**
 * Usage :** ` import { MpFlex } from '@mekari/pixel' `
 **/

const MpFlex = {
  name: 'MpFlex',
  mixins: [createStyledAttrsMixin('MpFlex')],
  props: flexProps,
  computed: {
    componentStyles() {
      return {
        display: 'flex',
        flexDirection: this.direction,
        alignItems: this.align,
        justifyContent: this.justify,
        flexWrap: this.wrap,
        h: this.size,
        w: this.size
      };
    }

  },

  render(h) {
    return h(this.as, {
      class: this.className,
      attrs: this.computedAttrs,
      on: this.computedListeners
    }, this.$slots.default);
  }

};

export { MpFlex, flexProps };
