import { MpBox } from '@mekari/pixel-box';
import { MpCoachmark } from '@mekari/pixel-coachmark';
import { ThemeProvider, ColorModeProvider } from '@mekari/pixel-provider';
import Vue from 'vue';
import { createStyledAttrsMixin, useId, colorModeObserver } from '@mekari/pixel-utils';
import { createPopper } from '@popperjs/core';

/**
 * Tour component is used to display product tour information.
 *
 * @category Data Display - Component
 * @see Docs - https://storybook.mekari.design/?path=/docs/components-tour--playground
 * @see NPMJS - https://www.npmjs.com/package/@mekari/pixel-tour
 */

const MpTour = {
  name: 'MpTour',
  mixins: [createStyledAttrsMixin('MpTour')],
  props: {
    id: {
      type: String
    },
    steps: {
      type: Array
    },
    stepIndex: {
      type: Number
    },
    isDisableScroll: {
      type: Boolean,
      default: false
    },
    isManual: {
      type: Boolean,
      default: false
    },
    isDebug: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      targetElement: null,
      refElement: null,
      index: this.stepIndex || 0
    };
  },

  watch: {
    index(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.createStep();
      }
    }

  },
  computed: {
    getId() {
      return this.id || `tour-${useId()}`;
    },

    getStepsLength() {
      return this.steps.length;
    },

    isLastStep() {
      return this.index === this.getStepsLength;
    }

  },
  methods: {
    createStep() {
      if (this.isLastStep) {
        return;
      }

      if (this.targetElement) {
        this.removeHighlight();
      }

      if (this.isDisableScroll) {
        document.body.style.overflow = 'hidden';
      }

      this.targetElement = document.querySelector(this.steps[this.index].target);
      this.refElement = this.$refs[this.getId].$el;

      if (this.isDebug) {
        console.log(`[MpTour] The target element of ${this.steps[this.index].target} is:`, this.targetElement);
        console.log(`[MpTour] The refs element of ${this.getId} is:`, this.refElement);
      }

      if (this.targetElement) {
        this.createHighlight();
        createPopper(this.targetElement, this.refElement, {
          modifiers: [{
            name: 'preventOverflow',
            options: {
              rootBoundary: 'window'
            }
          }, {
            name: 'offset',
            options: {
              offset: [0, 16]
            }
          }],
          placement: 'bottom',
          ...this.steps[this.index].options
        });
      }
    },

    scrollToTargetElement(offset) {
      const element = this.targetElement;
      const headerOffset = offset !== undefined ? offset : 16;
      const elementPosition = element.getBoundingClientRect().top;
      const offsetPosition = elementPosition + window.pageYOffset - headerOffset;
      window.scrollTo({
        top: offsetPosition,
        behavior: "smooth"
      });
    },

    createHighlight() {
      this.targetElement.style.zIndex = '9999';
      this.targetElement.style.position = 'relative';
      this.targetElement.style.pointerEvents = 'none';

      if (this.steps[this.index].isScrollIntoElement) {
        this.scrollToTargetElement(this.steps[this.index].offset);
      }

      if (this.steps[this.index].isHighlighted) {
        this.createPulseAnimation();
      }
    },

    createPulseAnimation() {
      // default rgba(35, 41, 51, 1) $dark
      let r = '35';
      let g = '41';
      let b = '51';

      if (this.steps[this.index].highlightColor === 'pink') {
        // rgba(236, 72, 153, 1) $pink-400
        r = '236';
        g = '72';
        b = '153';
      }

      if (this.steps[this.index].highlightColor === 'blue') {
        // rgba(59, 130, 240, 1) $sky-400
        r = '59';
        g = '130';
        b = '240';
      }

      this.targetElement.classList.add('tour-pulse');
      const style = document.createElement('style');
      style.textContent = `.tour-pulse {
        box-shadow: 0 0 0 0 rgba(${r}, ${g}, ${b}, 1);
        animation: pulse 2s infinite;
      }

      @keyframes pulse {
        0% {
          box-shadow: 0 0 0 0 rgba(${r}, ${g}, ${b}, 0.9);
        }

        70% {
          box-shadow: 0 0 0 10px rgba(${r}, ${g}, ${b}, 0);
        }

        100% {
          box-shadow: 0 0 0 0 rgba(${r}, ${g}, ${b}, 0);
        }
      }`;
      document.head.appendChild(style);
    },

    removeHighlight() {
      this.targetElement.style.zIndex = null;
      this.targetElement.style.position = null;
      this.targetElement.style.pointerEvents = null;
      this.targetElement.classList.remove('tour-pulse');
    },

    handleNext() {
      if (!this.isManual && this.index < this.getStepsLength) {
        this.index += 1;
      }

      if (this.targetElement) {
        this.removeHighlight();
      }

      if (this.isDisableScroll) {
        document.body.style.overflow = null;
      }

      this.$emit('next', this.index);
    },

    handlePrev() {
      if (!this.isManual && this.index > 0) {
        this.index -= 1;
      }

      if (this.targetElement) {
        this.removeHighlight();
      }

      if (this.isDisableScroll) {
        document.body.style.overflow = null;
      }

      this.$emit('prev', this.index);
    },

    handleClose() {
      if (this.targetElement) {
        this.removeHighlight();
      }

      if (this.isDisableScroll) {
        document.body.style.overflow = null;
      }

      this.$emit('close', this.index);
    }

  },

  mounted() {
    this.createStep();
  },

  render(h) {
    return h(MpBox, {
      attrs: {
        id: this.getId,
        'data-pixel-component': 'MpTour',
        ...this.splitProps.styleAttrs,
        ...this.computedAttrs
      }
    }, [h(MpBox, {
      attrs: {
        zIndex: '9998',
        position: 'fixed',
        top: '0',
        right: '0',
        bottom: '0',
        left: '0',
        width: 'full',
        height: 'full',
        background: 'overlay'
      }
    }), h(MpCoachmark, {
      ref: this.getId,
      props: { ...(this.steps[this.index] && this.steps[this.index].content)
      },
      attrs: {
        zIndex: '9999',
        position: !this.targetElement && 'absolute',
        top: !this.targetElement && '50%',
        left: !this.targetElement && '50%',
        transform: !this.targetElement && 'translate(-50%, -50%);'
      },
      on: {
        'click-primary-action': this.handleNext,
        'click-secondary-action': this.handlePrev,
        'click-close-button': this.handleClose
      }
    })]);
  }

};
const isBrowser = typeof window !== 'undefined' && typeof window.document !== 'undefined';
const PORTAL_ID = 'pixel-tour-portal';
const ROOT_ID = 'pixel-tour-root';
const VM = typeof window !== 'undefined' && window.Vue ? window.Vue : Vue;

function createPortal() {
  if (!isBrowser) {
    return;
  }

  let portalElement;
  const existingPortalElement = document.getElementById(PORTAL_ID);

  if (existingPortalElement) {
    portalElement = existingPortalElement;
  } else {
    const el = document.createElement('div');
    el.id = PORTAL_ID;
    el.className = 'Tour';

    if (document.body != null) {
      document.body.appendChild(el);
    }

    portalElement = el;
  } // Create pixel tour root instance


  const root = document.createElement('div');
  root.id = ROOT_ID;
  portalElement.appendChild(root);
  return portalElement;
}

function emptyHandle() {}

function useTour() {
  const {
    theme,
    colorMode
  } = colorModeObserver;

  function tour(isStart = false, steps = [], options = {}) {
    if (isStart) {
      createPortal();
      return new VM({
        el: `#${ROOT_ID}`,

        render(h) {
          return h(ThemeProvider, {
            props: {
              theme
            }
          }, [h(ColorModeProvider, {
            props: {
              value: colorMode || 'light'
            }
          }, [isStart && h(MpTour, {
            props: {
              id: options.id,
              steps: steps,
              stepIndex: options.stepIndex,
              isDisableScroll: options.isDisableScroll,
              isManual: options.isManual,
              isDebug: options.isDebug
            },
            on: {
              next: options.next || emptyHandle,
              prev: options.prev || emptyHandle,
              close: options.close || emptyHandle
            }
          })])]);
        }

      }).$mount();
    }

    return document.getElementById(PORTAL_ID) && document.getElementById(PORTAL_ID).remove();
  }

  return tour;
}

export { MpTour, useTour };
