import { createStyledAttrsMixin, extractListeners } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';
import { MpAnimateHeight } from '@mekari/pixel-transition';

const MpCollapse = {
  name: 'MpCollapse',
  mixins: [createStyledAttrsMixin('MpCollapse')],
  props: {
    as: {
      type: [String, Object],
      default: 'div'
    },
    isOpen: Boolean,
    duration: {
      type: Number,
      default: 250
    },
    easing: {
      type: String,
      default: 'easeInOutSine'
    },
    startingHeight: Number,
    endingHeight: Number,
    animateOpacity: {
      type: Boolean,
      default: true
    }
  },

  render(h) {
    const children = this.$slots.default;
    const nonNativeEvents = {
      start: e => {
        const emitStart = this.$listeners.start;

        if (emitStart) {
          emitStart('start', e);
        }
      },
      finish: e => {
        const emitFinish = this.$listeners.finish;

        if (emitFinish) {
          emitFinish('finish', e);
        }
      }
    };
    const {
      native,
      nonNative
    } = extractListeners(this.$listeners, nonNativeEvents);
    return h(MpAnimateHeight, {
      props: {
        isOpen: this.$props.isOpen,
        duration: this.$props.duration,
        enterEasing: this.$props.easing,
        leaveEasing: this.$props.easing,
        initialHeight: this.$props.startingHeight,
        finalHeight: this.$props.endingHeight,
        animateOpacity: this.$props.animateOpacity
      },
      on: nonNative,
      nativeOn: native,
      attrs: {
        'data-pixel-component': 'MpCollapse'
      }
    }, [h(MpBox, {
      class: this.className,
      props: {
        as: this.$props.as
      },
      attrs: { ...this.computedAttrs
      }
    }, children)]);
  }

}; // import { extractListeners } from '@mekari/pixel-utils'
