import { createStyledAttrsMixin } from '@mekari/pixel-utils';

/**
 * Usage :** ` import { MpBox } from '@mekari/pixel' `
 **/

const MpBox = {
  name: 'MpBox',
  mixins: [createStyledAttrsMixin('MpBox')],
  props: {
    as: {
      type: [String, Object],
      default: 'div'
    },
    to: {
      type: [String, Object],
      default: ''
    }
  },

  render(h) {
    return h(this.as, {
      props: {
        to: this.to
      },
      class: this.className,
      on: this.listeners$,
      attrs: this.computedAttrs
    }, this.$slots.default);
  }

};

export { MpBox };
