import { MpBox } from '@mekari/pixel-box';
import { MpFlex } from '@mekari/pixel-flex';
import { MpSpinner } from '@mekari/pixel-spinner';
import { createStyledAttrsMixin, useId } from '@mekari/pixel-utils';

const MpImage = {
  name: 'MpImage',
  mixins: [createStyledAttrsMixin('MpImage')],
  props: {
    id: {
      type: String
    },
    src: {
      type: String
    },
    alt: {
      type: String
    },
    layout: {
      type: String,
      default: 'intrinsic'
    },
    width: {
      type: Number
    },
    height: {
      type: Number
    },
    sizes: {
      type: String
    },
    srcPlaceholder: {
      type: String,
      default: 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
    },
    srcset: {
      type: String
    },
    typeSrcset: {
      type: String
    },
    mediaSrcset: {
      type: String
    },
    maxSrcset: {
      type: Number,
      default: 5
    },
    intersectionOptions: {
      type: Object,
      default: () => ({})
    },
    objectFit: {
      type: String
    },
    objectPosition: {
      type: String
    },
    errorText: {
      type: String,
      default: 'Failed load image'
    },
    isLazy: {
      type: Boolean,
      default: true
    },
    isShowLoading: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      observer: null,
      intersected: false,
      loaded: false,
      failed: false
    };
  },

  computed: {
    getId() {
      return this.id || `image-${useId()}`;
    },

    isVisible() {
      return this.intersected || !this.isLazy;
    },

    isLoading() {
      return this.isShowLoading && this.isVisible && !this.loaded && !this.failed;
    },

    srcImage() {
      return this.isVisible && this.src ? this.src : this.srcPlaceholder;
    },

    srcsetImage() {
      return this.isVisible && this.srcset ? this.srcset : false;
    },

    sizerSvg() {
      if (this.layout !== 'intrinsic') {
        return undefined;
      }

      const isShowBackgroundColor = !this.intersected;
      return `<svg width="${this.width}" height="${this.height}" xmlns="http://www.w3.org/2000/svg"
      ${isShowBackgroundColor ? 'style="background-color:#ffffff;"' : ''}>
      ${this.failed ? `<text x="50%" y="50%" dominant-baseline="middle" text-anchor="middle">${this.errorText}</text>` : ''}</svg>`;
    },

    sizerStyle() {
      let style;

      if (this.layout === 'responsive') {
        const quotient = this.height / this.width;
        const paddingTop = isNaN(quotient) ? '100%' : `${quotient * 100}%`;
        style = {
          display: 'block',
          boxSizing: 'border-box',
          paddingTop
        };
      } else if (this.layout === 'intrinsic') {
        style = {
          boxSizing: 'border-box',
          display: 'block',
          maxWidth: '100%'
        };
      }

      return style;
    },

    wrapperStyle() {
      let style = {};

      if (this.layout === 'responsive') {
        // <MpImage src="i.png" width="100" height="100" layout="responsive" />
        style = {
          position: 'relative',
          display: 'block',
          overflow: 'hidden',
          boxSizing: 'border-box',
          margin: 0
        };
      } else if (this.layout === 'intrinsic') {
        // <MpImage src="i.png" width="100" height="100" layout="intrinsic" />
        style = {
          position: 'relative',
          display: 'inline-block',
          overflow: 'hidden',
          boxSizing: 'border-box',
          maxWidth: '100%',
          margin: 0
        };
      } else if (this.layout === 'fixed') {
        // <MpImage src="i.png" width="100" height="100" layout="fixed" />
        style = {
          position: 'relative',
          display: 'inline-block',
          overflow: 'hidden',
          boxSizing: 'border-box',
          width: `${this.width}px`,
          height: `${this.height}px`
        };
      } else if (this.layout === 'fill') {
        // <MpImage src="i.png" layout="fill" />
        style = {
          position: 'absolute',
          display: 'block',
          overflow: 'hidden',
          boxSizing: 'border-box',
          top: 0,
          left: 0,
          bottom: 0,
          right: 0,
          margin: 0
        };
      }

      return style;
    }

  },

  mounted() {
    // lazy-loading
    if ('IntersectionObserver' in window) {
      this.observer = new IntersectionObserver(entries => {
        const image = entries[0];

        if (image.isIntersecting) {
          this.intersected = true;
          this.observer.disconnect();
          this.$emit('intersect', this.getId);
        }
      }, this.intersectionOptions);
      this.observer.observe(this.$el);
    } // logging in development


    if (process.env.NODE_ENV !== 'production') {
      const NEED_WIDTH_HEIGHT = ['intrinsic', 'fixed'];

      if (NEED_WIDTH_HEIGHT.includes(this.layout) && (this.width === undefined || this.height === undefined)) {
        console.warn('Image with layout="intrinsic" or layout="fixed" MUST have height and width prop');
      }
    }
  },

  destroyed() {
    if ('IntersectionObserver' in window) {
      this.observer.disconnect();
    }
  },

  methods: {
    toBase64(str) {
      if (typeof window === 'undefined') {
        return Buffer.from(str).toString('base64');
      } else {
        return window.btoa(str);
      }
    },

    handleLoad() {
      if (this.$refs.img && this.$refs.img.getAttribute('src') !== this.srcPlaceholder && !this.loaded) {
        this.loaded = true;
        this.$emit('load', this.getId);
      }
    },

    handleError(e) {
      this.$emit('error', this.getId, e);
      this.failed = true;
    }

  },

  render(h) {
    return h(MpBox, {
      attrs: {
        id: this.getId,
        'data-pixel-component': 'MpImage',
        ...this.splitProps.styleAttrs,
        ...this.computedAttrs,
        ...this.wrapperStyle
      }
    }, [this.isLoading && h(MpFlex, {
      props: {
        align: 'center',
        justify: 'center'
      },
      attrs: {
        zIndex: 1,
        position: 'absolute',
        width: 'full',
        height: 'full'
      }
    }, [this.$slots.default ? this.$slots.default : h(MpSpinner)]), this.sizerStyle && h(MpBox, {
      attrs: { ...this.sizerStyle
      }
    }, [this.sizerSvg && h('img', {
      attrs: {
        src: `data:image/svg+xml;base64,${this.toBase64(this.sizerSvg)}`,
        alt: '',
        ariaHidden: true
      },
      style: {
        maxWidth: '100%',
        display: 'block',
        border: 'none',
        margin: 0,
        padding: 0
      }
    })]), h('picture', {}, [h('source', {
      attrs: {
        srcset: this.srcsetImage,
        type: this.typeSrcset,
        media: this.mediaSrcset
      }
    }), h('img', {
      ref: 'img',
      attrs: {
        alt: this.alt,
        src: this.srcImage,
        sizes: this.sizes,
        decoding: 'async'
      },
      style: {
        position: 'absolute',
        display: 'block',
        top: 0,
        left: 0,
        bottom: 0,
        right: 0,
        boxSizing: 'border-box',
        padding: 0,
        border: 'none',
        margin: 'auto',
        width: '0',
        height: '0',
        minWidth: '100%',
        maxWidth: '100%',
        minHeight: '100%',
        maxHeight: '100%',
        objectFit: this.objectFit,
        objectPosition: this.objectPosition
      },
      on: {
        load: this.handleLoad,
        error: this.handleError
      }
    })])]);
  }

};

export { MpImage };
