import { createStyledAttrsMixin } from '@mekari/pixel-utils';
import { keyframes } from '@emotion/css';
import { MpVisuallyHidden } from '@mekari/pixel-visually-hidden';

const spin = keyframes`
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
`;
/**
 * Usage :** ` import { MpSpinner } from '@mekari/pixel' `
 **/

const MpSpinner = {
  name: 'MpSpinner',
  mixins: [createStyledAttrsMixin('MpSpinner')],
  props: {
    size: {
      type: String,
      default: 'md'
    },
    label: {
      type: String,
      default: 'Loading...'
    },
    thickness: {
      type: String,
      default: '2px'
    },
    speed: {
      type: String,
      default: '0.45s'
    },
    color: {
      type: String,
      default: 'gray.600'
    },
    emptyColor: {
      type: String,
      default: 'transparent'
    }
  },
  computed: {
    componentStyles() {
      return {
        d: 'inline-block',
        borderWidth: this.thickness,
        borderColor: 'currentColor',
        borderStyle: 'solid',
        rounded: 'full',
        color: this.color,
        borderBottomColor: this.emptyColor,
        borderLeftColor: this.emptyColor,
        animation: `${spin} ${this.speed} linear infinite`,
        w: this.size === 'sm' ? 5 : 6,
        h: this.size === 'sm' ? 5 : 6
      };
    }

  },

  render(h) {
    return h('div', {
      class: this.className,
      attrs: { // ...setSizes(this.size),
        ...this.computedAttrs
      }
    }, this.label && h(MpVisuallyHidden, {}, this.label));
  }

};

export { MpSpinner };
