import Breadstick from 'breadstick';
import { colorModeObserver } from '@mekari/pixel-utils';
import { MpBox } from '@mekari/pixel-box';
import { MpIcon } from '@mekari/pixel-icon';
import { ThemeProvider, ColorModeProvider } from '@mekari/pixel-provider';

const breadstick = new Breadstick();
const variants = {
  success: {
    color: 'green.700',
    icon: {
      name: 'done',
      variant: 'duotone',
      size: 'md'
    }
  },
  error: {
    color: 'red.700',
    icon: {
      name: 'error',
      variant: 'duotone',
      size: 'md'
    }
  },
  info: {
    color: 'blue.700',
    icon: {
      name: 'info',
      variant: 'duotone',
      size: 'md'
    }
  },
  warning: {
    color: 'orange.700',
    icon: {
      name: 'warning-triangle',
      variant: 'duotone',
      size: 'md'
    }
  }
};
/**
 * Usage :** ` import { MpToast } from '@mekari/pixel-toast' `
 **/

const MpToast = {
  name: 'MpToast',
  inheritAttrs: false,
  props: {
    variant: {
      type: String,
      default: 'success'
    },

    /** Toast ID */
    id: {
      type: String
    },

    /** The title of the toast. **/
    title: {
      type: String,
      default: ''
    }
  },

  created() {
    if (this.variant === 'info' || this.variant === 'warning') console.warn('[Pixel warn]: Deprecated on `variant` prop `info` and `warning`.');
  },

  render(h) {
    return h(MpBox, {
      attrs: {
        'data-pixel-component': 'MpToast',
        border: '1.5px',
        p: '3',
        'border-radius': 'md',
        'align-items': 'center',
        'border-color': variants[this.variant].color,
        bg: 'white',
        m: '10',
        shadow: 'lg',
        lineHeight: 0,
        zIndex: '9999'
      }
    }, [h(MpIcon, {
      props: variants[this.variant].icon,
      attrs: {
        mr: '2'
      }
    }), h(MpBox, {
      attrs: {
        as: 'span',
        'font-size': 'md'
      }
    }, this.title)]);
  }

};

function useToast() {
  const DEFAULT_ZINDEX = 5500;
  const {
    theme,
    colorMode
  } = colorModeObserver;

  function notify({
    position = 'bottom',
    duration = 5000,
    title,
    description,
    variant = 'solid',
    zIndex = DEFAULT_ZINDEX
  }) {
    const options = {
      position,
      duration
    };
    breadstick.notify(({
      h,
      id
    }) => {
      return h(ThemeProvider, {
        props: {
          theme
        }
      }, [h(ColorModeProvider, {
        props: {
          value: colorMode || 'light'
        }
      }, [h(MpToast, {
        props: {
          variant,
          id: `${id}`,
          title,
          description
        }
      })])]);
    }, options);
    const el = document.getElementsByClassName(`Breadstick__manager-${position}`);

    if (zIndex !== DEFAULT_ZINDEX) {
      el[0].style.zIndex = zIndex;
    } else {
      el[0].style.zIndex = DEFAULT_ZINDEX;
    }
  }

  return notify;
}

export { MpToast, useToast };
