import { injectGlobal } from '@emotion/css';
import { colorModeObserver, colorModeObserverEventBus } from '@mekari/pixel-utils';

const {
  version
} = require('@mekari/pixel/package.json');

const ThemeProvider = {
  name: 'ThemeProvider',

  provide() {
    return {
      $pixelTheme: () => this.theme,
      $pixelIcons: this.icons,
      $pixelColorMode: () => 'light',
      $pixelVersion: version
    };
  },

  props: {
    rootKey: {
      type: String,
      default: ':root'
    },
    isShowVersion: {
      type: Boolean,
      default: true
    }
  },

  mounted() {
    if (this.isShowVersion) {
      console.info(`%cPixel Version: ${version}`, 'color: #651FFF; background: #F1F5F9;');
    }
  },

  computed: {
    icons() {
      return this.$pixel ? this.$pixel.icons : {};
    },

    theme() {
      return this.$pixel.theme;
    }

  },
  watch: {
    theme: {
      immediate: true,

      handler(newVal) {
        colorModeObserver.theme = newVal;
        this.updateGlobalCssVars();
      }

    },
    icons: {
      immediate: true,

      handler(newVal) {
        colorModeObserver.icons = newVal;
      }

    }
  },
  methods: {
    updateGlobalCssVars() {
      const rootKey = this.rootKey || ':root';
      injectGlobal({
        [rootKey]: this.theme.__cssVars
      });
    }

  },

  render(h) {
    return h('div', {
      attrs: {
        id: '__pixel-app',
        'data-pixel-component': 'ThemeProvider'
      }
    }, this.$slots.default);
  }

};

const ColorModeProvider = {
  name: 'ColorModeProvider',
  props: {
    value: String
  },

  data() {
    return {
      colorMode: 'light'
    };
  },

  provide() {
    return {
      $pixelColorMode: () => this._colorMode,
      $toggleColorMode: this.toggleColorMode
    };
  },

  computed: {
    _colorMode: {
      get() {
        return this.value ? this.value : this.colorMode;
      },

      set(value) {
        this.colorMode = value;
      }

    }
  },

  created() {
    colorModeObserverEventBus.$emit('change:colorMode', this._colorMode);
    this.$watch(() => this._colorMode, newColorMode => {
      colorModeObserverEventBus.$emit('change:colorMode', newColorMode);
    });
    colorModeObserverEventBus.$on('command:toggleColorMode', this.toggleColorMode);
  },

  methods: {
    toggleColorMode() {
      /** Toggles colorMode */
      this._colorMode = this._colorMode === 'light' ? 'dark' : 'light';
    }

  },

  render() {
    return this.$scopedSlots.default({
      colorMode: this._colorMode,
      toggleColorMode: this.toggleColorMode
    });
  }

};
const DarkMode = {
  name: 'DarkMode',

  render(h) {
    return h(ColorModeProvider, {
      props: {
        value: 'dark'
      }
    }, this.$slots.default);
  }

};
const LightMode = {
  name: 'LightMode',

  render(h) {
    return h(ColorModeProvider, {
      props: {
        value: 'light'
      }
    }, this.$slots.default);
  }

};

export { ColorModeProvider, DarkMode, LightMode, ThemeProvider };
